import React, { useState, useEffect, useCallback } from 'react';
import { fetchMovies } from '../../util/Movie';
import { isLoggedIn } from '../../util/Auth';
import './Popular.css';

function Popular({ apiKey }) {  // API  props 
    const [movies, setMovies] = useState([]);
    const [page, setPage] = useState(1);
    const [isLoading, setIsLoading] = useState(false);
    const [showTopButton, setShowTopButton] = useState(false); // Top   

    // loadMovies  useCallback 
    const loadMovies = useCallback(async () => {
        setIsLoading(true);

        // page apiKey  
        const popularMovies = await fetchMovies('/movie/popular', apiKey, {
            page,
            language: 'ko-KR' //    
        });

        //   
        setMovies((prevMovies) => {
            const newMovies = popularMovies.filter(
                (movie) => !prevMovies.some((prevMovie) => prevMovie.id === movie.id)
            );
            return [...prevMovies, ...newMovies];
        });

        setIsLoading(false);
    }, [page, apiKey]); // page apiKey   loadMovies     

    useEffect(() => {
        loadMovies();
    }, [loadMovies]); // loadMovies   

    useEffect(() => {
        const handleScroll = () => {
            setShowTopButton(window.scrollY > 200); //  200px  Top  
            if (window.innerHeight + document.documentElement.scrollTop + 1 >= document.documentElement.scrollHeight && !isLoading) {
                setPage((prevPage) => prevPage + 1);
            }
        };

        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, [isLoading]);

    const scrollToTop = () => {
        window.scrollTo({ top: 0, behavior: 'smooth' });
    };

    const handleDoubleClick = (movie) => {
        if (!isLoggedIn()) {
            alert(" .");
            return;
        }
        const wishlist = JSON.parse(localStorage.getItem('wishlist')) || [];
        const movieExists = wishlist.find((item) => item.id === movie.id);
        if (!movieExists) {
            wishlist.push(movie);
            localStorage.setItem('wishlist', JSON.stringify(wishlist));
            alert(`${movie.title}()  .`);
        } else {
            alert("   .");
        }
    };

    return (
        <div className="popular">
            <h2>Popular Movies</h2>
            <div className="movies-grid">
                {movies.map((movie) => (
                    <div key={movie.id} className="movie-card" onDoubleClick={() => handleDoubleClick(movie)}>
                        <img src={`https://image.tmdb.org/t/p/w500${movie.poster_path}`} alt={movie.title} />
                    </div>
                ))}
            </div>
            {showTopButton && (
                <button className="top-button" onClick={scrollToTop}>
                    Top
                </button>
            )}
            {isLoading && <p className="loading">Loading...</p>}
        </div>
    );
}

export default Popular;
